@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
@font-face {
  font-family: 'Poppins';
  font-style: normal;
  /* font-weight: 400; */
  src: url(/my-todo-list/static/media/Poppins-Medium.b59f6a63.ttf);
}

@font-face {
  font-family: 'Orbitron';
  font-style: normal;
  font-weight: 400;
  src: url(/my-todo-list/static/media/Orbitron-Black.cc37f7b5.ttf);
}

.todo-app{
  margin-top:120px;
}

.time-font {
  font-family: 'Poppins';
  font-weight: 300;
  margin-right:10px;
}
.nav-wrapper {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.nav-flex{
 font-size: 30px;
} 

.time-font2{
  font-family: 'Orbitron';
  font-weight: 300;
  margin-right:10px;
  letter-spacing: 2px;
  
}
@media (max-width:700px) {
  .nav-wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    height: auto;
  }

  .nav-flex{
    font-size: 23px;
  } 
}

.center {
  padding-bottom: 10px;
  font-family: 'Roboto';
}
